import React from "react"
import Favorites from "../../utils/favorites"
import ProgressBar from "react-bootstrap/ProgressBar"
import Button from "react-bootstrap/Button"
import { graphql, navigate, useStaticQuery } from "gatsby"


const TestWords = ({ wordsData }) => {


  const favorites = new Favorites()
  const [pos, setPos] = React.useState([0, 1])
  const [isFavorite, setIsFavorite] = React.useState(favorites.storageFindIndexWord(wordsData[pos[0]].question) !== -1)
  const [answers, setAnswers] = React.useState({correct: 0, wrong: 0})
  let currentAnswer = true;

  const next = () => {
    if (pos[0] < wordsData.length - 1) {
      setPos([pos[0] + 1, 1])
      setIsFavorite(favorites.storageFindIndexWord(wordsData[pos[0] + 1].question) !== -1)
      if (currentAnswer){
        setAnswers({correct: answers.correct + 1, wrong: answers.wrong})
      } else {
        setAnswers({correct: answers.correct, wrong: answers.wrong + 1})
      }
      currentAnswer = true;
    } else {
      navigate("/test")
      return null
    }
  }

  const onFavoriteClick = () => {
    if (isFavorite) {
      favorites.storageRemoveWord(wordsData[pos[0]].question)
      setIsFavorite(!isFavorite)
    } else {
      favorites.storageAddWord(wordsData[pos[0]].question)
      setIsFavorite(!isFavorite)
    }
  }
  const favoriteLabel = isFavorite ? "❌" : "❤️"
  const favoriteColor = isFavorite ? { backgroundColor: "#ff99cd" } : null
  const Variant = ({ variant, answer, next, setWrongAnswer }) => {
    const [setting, setSetting] = React.useState({
      style: "variant btn btn-light mb-3 col-12",
      lang: 0
    })

    return (
      <div className={setting.style} onClick={() => {
        if (variant[0] === answer) {
          setSetting({
            style: "variant btn btn-success mb-3 col-12",
            lang: 0
          })
          setTimeout(()=>{
            next();
          }, 1000)

        } else {
            setWrongAnswer();
            setSetting({
              style: "variant btn btn-danger mb-3 col-12",
              lang: setting.lang === 0 ? 1 : 0
            })
        }
      }}>{variant[setting.lang]}</div>)

  }
  return (<>

      <ProgressBar>
        <ProgressBar variant="success" now={answers.correct}
                     label={`${answers.correct}/ ${wordsData.length}`} key={1} min="0" max={wordsData.length-answers.wrong+1}/>
        <ProgressBar variant="danger" now={answers.wrong}
                     label={answers.wrong ? `${answers.wrong}/ ${wordsData.length}`: null}
                     key={2} max={wordsData.length-answers.wrong+1}/>
      </ProgressBar>
      <div className="row">
        <Button className="col mx-3 my-1" style={favoriteColor} variant="outline-light" onClick={onFavoriteClick}
                key={pos}>{favoriteLabel}</Button>
      </div>
      <div className="english-words">
        <div className="word-wrapper">
          <h1 className="word">{wordsData[pos[0]].question[1]}</h1>
        </div>
        <div className="w-100 row px-3">
          {wordsData[pos[0]].variants.map((el, key) => (
            <Variant key={key} answer={wordsData[pos[0]].question[0]} next={next} variant={el} setWrongAnswer={()=>currentAnswer=false}/>))}
        </div>
      </div>
      <div className="row">
        <Button className="col btn btn-warning mx-3 my-2" onClick={()=>navigate("/test")}>Abort test</Button>
      </div>
    </>
  )
}

export default TestWords
