class Favorites {
  storageAddWord = (word) => {
    let favorites = localStorage.getItem('favorites');
    if (!favorites) {
      const arr = []
      arr.push(word);
      favorites = {
        "label": "repeat ❤️",
        "id": 0,
        "values": arr
      }
    } else {
     favorites = JSON.parse(favorites);
     favorites.values.push(word);
    }
    favorites = JSON.stringify(favorites);
    localStorage.setItem('favorites', favorites);
  }

  storageFindIndexWord = (word) => {
    const favorites = localStorage.getItem('favorites');
    if (!favorites) {
      return -1;
    }
    const favoritesWords = JSON.parse(favorites).values;
    return favoritesWords.findIndex((el) => el[0] === word[0] && el[1] === word[1]);
  }


  storageRemoveWord = (word) => {
    let favorites = localStorage.getItem('favorites');
    if (favorites) {
      favorites = JSON.parse(favorites)
      const index = favorites.values.findIndex((el) => (el[0] === word[0] && el[1] === word[1]));
      if (index !== -1) {
        favorites.values.splice(index, 1);
        if (favorites.values.length === 0) {
          localStorage.removeItem('favorites')
        } else {
          localStorage.setItem('favorites', JSON.stringify(favorites));
        }
      }
    }
  }
}
export default Favorites;
